@import url(https://fonts.googleapis.com/css?family=Montserrat|Quicksand);
*{
    font-family: 'quicksand',Arial, Helvetica, sans-serif;
    box-sizing: border-box;
}

body{
    background:#fff;
}

.form-modal{
    position:relative;
    width:450px;
    height:auto;
    margin-top:4em;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
    background:#fff;
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
    box-shadow:0 3px 20px 0px rgba(0, 0, 0, 0.1)
}

.form-modal button{
    cursor: pointer;
    position: relative;
    text-transform: capitalize;
    font-size:1em;
    z-index: 2;
    outline: none;
    background:#fff;
    transition:0.2s;
}

.form-modal .btn{
    border-radius: 20px;
    border:none;
    font-weight: bold;
    font-size:1.2em;
    padding:0.8em 1em 0.8em 1em!important;
    transition:0.5s;
    border:1px solid #ebebeb;
    margin-bottom:0.5em;
    margin-top:0.5em;
}

.form-modal .login , .form-modal .signup{
    background:#57b846;
    color:#fff;
}

.form-modal .login:hover , .form-modal .signup:hover{
    background:#222;
}

.form-toggle{
    position: relative;
    width:100%;
    height:auto;
}

.form-toggle button{
    width:100%;
    float:left;
    padding:1.5em;
    margin-bottom:1.5em;
    border:none;
    transition: 0.2s;
    font-size:1.1em;
    font-weight: bold;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}

.form-toggle button:nth-child(1){
    border-bottom-right-radius: 20px;
}

.form-toggle button:nth-child(2){
    border-bottom-left-radius: 20px;
}

#login-toggle{
    background:#57b846;
    color:#ffff;
}

.form-modal form{
    position: relative;
    width:90%;
    height:auto;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);  
}

#login-form , #signup-form{
    position:relative;
    width:100%;
    height:auto;
    padding-bottom:1em;
}


#login-form button , #signup-form button{
    width:100%;
    margin-top:0.5em;
    padding:0.6em;
}

.form-modal input{
    position: relative;
    width:100%;
    font-size:1em;
    padding:1.2em 1.7em 1.2em 1.7em;
    margin-top:0.6em;
    margin-bottom:0.6em;
    border-radius: 20px;
    border:none;
    background:#ebebeb;
    outline:none;
    font-weight: bold;
    transition:0.4s;
}

.form-modal input:focus , .form-modal input:active{
    -webkit-transform:scaleX(1.02);
            transform:scaleX(1.02);
}

.form-modal input::-webkit-input-placeholder{
    color:#222;
}


.form-modal p{
    font-size:16px;
    font-weight: bold;
}

.form-modal p a{
    color:#57b846;
    text-decoration: none;
    transition:0.2s;
}

.form-modal p a:hover{
    color:#222;
}


.form-modal i {
    position: absolute;
    left:10%;
    top:50%;
    -webkit-transform:translateX(-10%) translateY(-50%);
            transform:translateX(-10%) translateY(-50%); 
}

.fa-google{
    color:#dd4b39;
}

.fa-linkedin{
    color:#3b5998;
}

.fa-windows{
    color:#0072c6;
}

.-box-sd-effect:hover{
    box-shadow: 0 4px 8px hsla(210,2%,84%,.2);
}

@media only screen and (max-width:500px){
    .form-modal{
        width:100%;
    }
}

@media only screen and (max-width:400px){
    i{
        display: none!important;
    }
}
:root {
    --navbar-expanded: .2em;
    --navbar-shrinked: 12.4em;
    --navbar-li-padding: .8em;
    --navbar-li-padding-quarter: 2em;
}

.navbar {
    font-weight: 600;
    height: 100%;
    width: 12.4em;
    width: var(--navbar-shrinked);
    position: fixed;
    background-color: var(--ghost-white);
    padding-left: .1em;
    transition: 0.5s;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    white-space: nowrap;
    -webkit-touch-callout: none;
}

.navbar li {
    padding: .8em;
    padding: var(--navbar-li-padding);
}
ul{
    list-style: none;
}

.navbar a {
    text-decoration: none;
    outline: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    transition: 0.3s;
    color: var(--space-cadet);
}

.navbar i {
    font-size: 1.5rem;
}

.navbar a.navbar-brand {
    color: var(--red-ryb);
}

.navbar a.navbar-brand,
.navbar a.navbar-footer {
    padding: 2em 2em 2em .8em;
    padding: var(--navbar-li-padding-quarter) var(--navbar-li-padding-quarter) var(--navbar-li-padding-quarter) var(--navbar-li-padding);
}

.navbar a:hover {
    color: var(--red-ryb);
}

.navbar span {
    margin-left: 1em;
    font-size: 1rem;
}

.navbar-nav span {
    vertical-align: middle;
}

main {
    transition: margin-left 0.5s;
    margin-left: 12.4em;
    margin-left: var(--navbar-shrinked);
}

body.navbar-expanded .navbar {
    width: .2em;
    width: var(--navbar-expanded);
}

body.navbar-expanded main {
    margin-left: .2em;
    margin-left: var(--navbar-expanded);
}

body.navbar-expanded li.active {
    margin-left: 4.5em;
    transition: .5s;
}

body.navbar-shrinked .navbar {
    width: 12.4em;
    width: var(--navbar-shrinked);
}

body.navbar-shrinked main {
    margin-left: 12.4em;
    margin-left: var(--navbar-shrinked);
}

body.navbar-shrinked li.active {
    margin: 0;
    transition: .5s;
}

.navbar .active {
    color: var(--red-ryb);
}

.navbar .active a {
    position: fixed;
    color: var(--red-ryb);
}

a{
    cursor: pointer;
}

@media screen and (max-height: 450px) {
    .navbar {
        padding-top: 15px;
    }
    .navbar a {
        font-size: 18px;
    }
}


:root {
    /* Reference colors */
    --blue-pantone: #3326ae;
    --red-ryb: #FF392Bff;
    --ghost-white: #F7F8FFff;
    --space-cadet: #2B2B4Cff;
    --amber: #FFC005ff;
    /* Custom colors*/
    --white: #fff;
    --white-transparent: #ffffff66;
    --space-cadet-transparent: #2B2B4Cbb;
    --placeholder: #cbcde4;
    --ghost-white-text: #c3c6deff;
    /* Tweet buttons */
    --video: var(--blue-pantone);
    --duration: #00c3f8;
    --likes: var(--red-ryb);
}

header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

section[role="search"] {
    background-color: #F7F8FFff;
    background-color: var(--ghost-white);
    padding: 1em 1.2em;
    border-radius: 1em;
    font-size: .75rem;
    width: 100%;
}

section[role="search"] input {
    border: none;
    background: none;
    margin-left: 0.5em;
    outline: none;
    font-size: 0.7rem;
    font-weight: 500;
    width: 100%;
    -webkit-appearance: none;
}

section[role="search"] input::-webkit-input-placeholder {
    color: #cbcde4;
    color: var(--placeholder);
}

section[role="search"] input:-ms-input-placeholder {
    color: #cbcde4;
    color: var(--placeholder);
}

section[role="search"] input::placeholder {
    color: #cbcde4;
    color: var(--placeholder);
}

section[role="search"] i {
    color: #3326ae;
    color: var(--blue-pantone);
    font-weight: bold;
}

.form-inline {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

section[role="application"] {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

nav.navbar-top a {
    text-decoration: none;
    color: #2B2B4Cff;
    color: var(--space-cadet);
    font-weight: 600;
    margin-left: 2em;
    font-size: 1rem;
}

.notification-profile {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.notification-profile a {
    text-decoration: none;
    color: #2B2B4Cbb;
    color: var(--space-cadet-transparent);
}

.notification-profile i {
    font-size: 1.1rem;
}

.notification-profile .profile-pic {
    background-image: url(/static/media/tweet-image.146c1b5e.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -.5em;
    width: 1.75em;
    height: 1.75em;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.notification-profile .notification {
    margin-right: 1.5em;
}
main {
    background-color: var(--white);
    border-radius: 3em 0 0 3em;
    padding: 3em;
    height: 100%;
}

.mainRadiusTop,
.mainRadiusBottom {
    position: absolute;
    width: 2.5em;
    height: 2.5em;
    margin: -3em;
    background-color: var(--ghost-white);
}

.mainRadiusBottom {
    margin-top: 1.4em;
}

.mainRadiusTop::after,
.mainRadiusBottom::after {
    content: '';
    width: 2.5em;
    height: 2.5em;
    background-color: var(--white);
    position: absolute;
    border-top-left-radius: 2.5em;
}

.mainRadiusBottom::after {
    border-top-left-radius: 0;
    border-bottom-left-radius: 2.5em;
}

.wrapper {
    display: -webkit-flex;
    display: flex;
    margin-top: 1em;
}

.tabContainer {
    display: none;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
}

.tab {
    border-radius: .5em;
    display: -webkit-flex;
    display: flex;
    background-color: var(--white);
    box-shadow: 0px 10px 18px 0px rgba(241, 243, 255, 1);
}

.tab a {
    text-decoration: none;
    padding: .5em 1.5rem;
    background-color: var(--white);
    border-radius: .5em;
}

.tab a:active {
    color: inherit;
}

.tab a.active {
    background-color: var(--duration);
    color: var(--white);
    border-radius: .5em;
}

section[role="region"] {
    height: 100%;
    width: calc(100% - 15em);
}

.info-box-container {
    display: -webkit-flex;
    display: flex;
    width: auto;
}

.info-box {
    display: -webkit-flex;
    display: flex;
    padding: 1em 1.5em;
    box-shadow: 0px 10px 18px 0px rgba(241, 243, 255, 1);
    border-radius: .75em;
    margin-right: 2em;
    width: 100%;
    cursor: pointer;
    -webkit-align-items: center;
            align-items: center;
}

.info-box.active {
    background-color: var(--red-ryb);
    color: var(--white);
    box-shadow: 0px 10px 18px 0px rgba(255, 57, 43, .5);
}

.info-box.active .info-content .header {
    color: var(--white-transparent);
}

.info-box img {
    width: 3em;
    height: 3em;
    border-radius: 1em;
}

.info-box .info-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 1em;
}

.info-box .info-content .header {
    text-transform: uppercase;
    color: var(--ghost-white-text);
    font-size: .75rem;
    font-weight: normal;
    margin-bottom: 1em;
}

.info-box .info-content .value {
    font-weight: 600;
    font-size: 2rem;
}


/* STATISTICS */

.statistics {
    margin-right: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 1.5em;
    background-color: var(--ghost-white);
    border-radius: .75em;
    padding: 1.5em;
}

.statistics header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    margin-bottom: 1em;
}

.statistics h1 {
    font-weight: 600;
}

.statistics select {
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: none;
    color: var(--blue-pantone);
    font-weight: 700;
    font-size: .8rem;
    padding: .75em 3.5em .75em 1em;
    border-radius: 1.5em;
    background-image: linear-gradient(45deg, transparent 50%, gray 50%), linear-gradient(135deg, gray 50%, transparent 50%);
    background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
    background-size: 5px 5px, 5px 5px, 1px 1.5em;
    background-repeat: no-repeat;
}

#statisticsChart {
    height: 10em;
}


/* SPENT TIME */

.bottomBox {
    margin-right: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 1.5em;
