    border-radius: .75em;
}

.bottomBox .spentTime {
    width: 100%;
    padding: 0;
}

.spentTime h1 {
    margin: 1em 1.5em;
}

.tweet {
    margin-left: 1.5em;
}

.tweet .profileContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.tweet .profileInfo {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-left: .75em;
}

.tweet .profileImage {
    background-image: url(/static/media/tweet-image.146c1b5e.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 2em;
    height: 2em;
    border-radius: 2em;
}

.tweet .profileInfo .name {
    font-size: 1rem;
