}

.tweet .badgesContainer {
    display: -webkit-flex;
    display: flex;
    margin-top: 1em;
    font-size: 1rem;
}

.tweetBadge i {
    margin-right: .25em;
}

.tweetBadge:nth-child(1) i {
    color: var(--video);
}

.tweetBadge:nth-child(2) i {
    color: var(--duration);
}

.tweetBadge:nth-child(3) i {
