}

.tweetBottomContainer {
    margin-top: 1.5em;
    font-size: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
